/**
 * This example shows how to use different programs to render nodes.
 * This works in two steps:
 * 1. You must declare all the different rendering programs to sigma when you
 *    instantiate it
 * 2. You must give to each node and edge a "type" value that matches a declared
 *    program
 * The programs offered by default by sigma are in src/rendering/webgl/programs,
 * but you can add your own.
 *
 * Here in this example, some nodes are drawn with images in them using the
 * the getNodeProgramImage provided by Sigma. Some others are drawn as white
 * disc with a border, and the custom program to draw them is in this directory:
 * - "./node.border.ts" is the node program. It tells sigma what data to give to
 *   the GPU and how.
 * - "./node.border.vert.glsl" is the vertex shader. It tells the GPU how to
 *   interpret the data provided by the program to obtain a node position,
 *   mostly.
 * - "./node.border.frag.glsl" is the fragment shader. It tells for each pixel
 *   what color it should get, relatively to data given by the program and its
 *   position inside the shape. Basically, the GPU wants to draw a square, but
 *   we "carve" a disc in it.
 */

import Graph from "graphology";
import Sigma from "sigma";

import getNodeProgramImage from "sigma/rendering/webgl/programs/node.image";
import NodeProgramBorder from "./node.border";
import ForceSupervisor from "graphology-layout-force/worker";

import Events from "./event"
import Epik from "./epik"

const container = document.getElementById("sigma-container") as HTMLElement;
const searchSpace = document.getElementById("space-input") as HTMLInputElement;

const searchTag = document.getElementById("tag-input") as HTMLInputElement;
const tagSuggestions = document.getElementById("tag-suggestions") as HTMLDataListElement;

const searchInput = document.getElementById("search-input") as HTMLInputElement;
const suggestions = document.getElementById("suggestions") as HTMLDataListElement;

const graph = new Graph();

let hoveredEdge = null;
// eslint-disable-next-line @typescript-eslint/no-unused-vars
const renderer = new Sigma(graph, container, {
  // We don't have to declare edgeProgramClasses here, because we only use the default ones ("line" and "arrow")
  nodeProgramClasses: {
    image: getNodeProgramImage(),
    border: NodeProgramBorder,
  },
  renderEdgeLabels: true,

  enableEdgeClickEvents: true,
  enableEdgeWheelEvents: true,
  enableEdgeHoverEvents: "debounce",
  edgeReducer(edge, data) {
    const res = { ...data };
    if (edge === hoveredEdge) res.color = "#cc0000";
    return res;
  },
});

// Create the spring layout and start it
const layout = new ForceSupervisor(graph);
layout.start();

const epik = new Epik(graph)
Events(graph, epik, hoveredEdge, renderer)
epik.explorer([])

searchSpace.value = epik.grapgSpace()
searchSpace.addEventListener("blur", () => {
  let keyword = searchSpace.value || ""
  epik.updateSpace(keyword)
});

searchTag.addEventListener("focus", () => {
  searchTagQuery()
});

async function searchTagQuery() {
  let sql = "SHOW TAGS;"
  let result = await epik.searchQuery(sql)
  if (result.code.code != 0) {
    return
  }
  result = result.data[0]
  let contents:string[] = []
  console.log("response:", result)
  for (let data of result.data) {
    let tag = data.row[0]
    // console.log("tag:", tag)
    let content = `<option value="${tag}"></option>`
    contents.push(content)
  }
  tagSuggestions.innerHTML = contents.join('\n')
}

searchTag.addEventListener("change", () => {
  searchQuery("")
});

searchInput.addEventListener("input", () => {
  let keyword = searchInput.value || ""
  searchQuery(keyword)
});

async function searchQuery(keyword:string) {
  let tag = searchTag.value || ""
  let sql = `MATCH (v:${tag}) RETURN v;`
  if (keyword.length > 0) {
    sql = `MATCH (v:${tag}{value:"${keyword}"}) return v;`
  }
  let result = await epik.searchQuery(sql)
  if (result.code.code != 0) {
    return
  }
  console.log("result:", result)
  result = result.data[0]
  let ids = []
  let contents: string[] = []
  for (let data of result.data) {
    let row = data.row[0]
    console.log(data)
    for (let p in row) {
      // console.log("p",p)
      let content = `<option value="${row[p]}"></option>`
      contents.push(content)
    }
    ids.push(data.meta[0].id)
  }
  suggestions.innerHTML = contents.join('\n')

  epik.explorer(ids)
}


