
import Graph from "graphology";
import chroma from "chroma-js";
import axios from "axios";


const nebulaUrl = "http://127.0.0.1:8080/graph/query"

const DefaultNebulaSpace = "CrossmodalSearch"
let testItems = ["b40e9e59-9c41-4a9e-93e4-75686035fe6f", "d1644d78-3cfb-4c62-9e98-099d2b780029","8dddb654-d2c4-45ba-a64a-fe569373294a"]

async function queryGraph(url, sql) {
    let params = {
      sql: sql,
    }
    let response = await axios.post(url, params)
    return response.data
  }
  
  async function addVertex(graph: Graph, data) {
    for (let idata of data) {
      for (let item of idata.data) {
        // console.log("item", item)
        for (let i in item.meta) {
          let metas = item.meta[i]
          for (let mi in metas) {
            let mitem = metas[mi]
    
            let rows = item.row[i]
            let ritem = rows[mi]
    
            if (mitem.type == 'vertex') {
              let type = "border"
              let label = ""
              let image = ""
              let size = 15
              for (let p in ritem) {
                let content = ritem[p]
                if (content.length > 100) {
                  try {
                    content = `${content}`
                    content = content.substring(2, content.length - 2)
                    // console.log("content:",content)
                    let contents = content.split("}")
                    // console.log("content:",contents)
                    // console.log("content[0]:",contents[0])
                    let item = JSON.parse(contents[0] + "}")
              
                    // console.log("item:",item)
              
                    if (item.type == "image") {
                      size = 30
                      type = "image"
                      image = "data:image/png;base64," + item.content
                      // console.log("content:", item.content)
                      // image = "https://imagepphcloud.thepaper.cn/pph/image/171/630/785.jpg"
                    }
                  } catch (e) {
                    console.log("error:", e)
                    label = ritem[p]
                  }
                } else {
                  label = content
                }
               
              }
              if (type == "image") {
                // graph.addNode(mitem.id, { size:90, height: 90, width: 90, label: label, type: type, image: image, color: chroma.random().hex() });
                graph.addNode(mitem.id, { size:size, label: label, type: type, image: image, color: chroma.random().hex() });
              } else {
                graph.addNode(mitem.id, { size: size, label: label, type: type, image: image, color: chroma.random().hex() });
              }
            } 
          }
        }
      } 
    }
  }
  
  async function addEdge(graph: Graph, data) {
    for (let idata of data) {
      for (let item of idata.data) {
        // console.log("item", item)
        for (let i in item.meta) {
          let metas = item.meta[i]
          for (let mi in metas) {
            let mitem = metas[mi]
    
            let rows = item.row[i]
            let ritem = rows[mi]
    
            if (mitem.type == 'edge') {
              // console.log("edge", mitem.id, ritem)
              graph.addEdge(mitem.id.src, mitem.id.dst, { type: "arrow", label: ritem.name, size: 5 });
            }
          }
        }
      } 
    }
  }
  
  function getRandomInt(max) {
    return Math.floor(Math.random() * max);
  }
  
  export default class EpikGraph {
    space: string
    graph: Graph
    constructor(graph: Graph) {
      this.graph = graph
      this.space = DefaultNebulaSpace
    }

    grapgSpace() {
      return this.space
    }

    updateSpace(space: string) {
      if (space.length > 0) {
        this.space = space
      }
      if (this.space != DefaultNebulaSpace) {
        testItems = []
      }
    }

   async addData(data:any) {
      let graph = this.graph
      graph.clear()
      await addVertex(graph, data.data)
      await addEdge(graph, data.data)
    
      graph.nodes().forEach((node, i) => {
        const angle = (i * 2 * Math.PI) / graph.order;
        graph.setNodeAttribute(node, "x", 100 * Math.cos(angle));
        graph.setNodeAttribute(node, "y", 100 * Math.sin(angle));
      });
   }

    async explorer(items: string[]) {
      if (items.length == 0) {
        items = testItems
      }

      if (items.length > 10) {
        items = items.slice(0, 10)
      }
      
      let graph = this.graph
      graph.clear()

      for (let item of items) {
        let sql = `USE ${this.space};GET SUBGRAPH WITH PROP 3 STEPS FROM '${item}';`
        let data = await queryGraph(nebulaUrl, sql)
        // console.log("data:", data.data)
      
        await addVertex(graph, data.data)
        await addEdge(graph, data.data)
      
        graph.nodes().forEach((node, i) => {
          const angle = (i * 2 * Math.PI) / graph.order;
          graph.setNodeAttribute(node, "x", 100 * Math.cos(angle));
          graph.setNodeAttribute(node, "y", 100 * Math.sin(angle));
        });
      }
    }

    async searchQuery(query: string) {
      let sql = `USE ${this.space};`
      sql = sql + query
      let result = await queryGraph(nebulaUrl, sql)
      return result
    }
  }