import Graph from "graphology";
import Sigma from "sigma";
import Epik from "./epik"

const logsDOM = document.getElementById("sigma-logs") as HTMLElement;


function logEvent(graph: Graph, epik: Epik, event: string, itemType?: "node" | "edge", item?: string): void {
    let message = `Event "${event}"`;
    if (item && itemType) {
      const label = itemType === "node" ? graph.getNodeAttribute(item, "label") : graph.getEdgeAttribute(item, "label");
      message += `, ${itemType} ${label || "with no label"} (id "${item}")`;
  
      if (itemType === "edge") {
        message += `, source ${graph.getSourceAttribute(item, "label")}, target: ${graph.getTargetAttribute(
          item,
          "label",
        )}`;
      }
    }
    console.log(message)
    // const div = document.createElement("div");
    // div.innerHTML = `<span>${message}</span>`;
    // logsDOM.appendChild(div);
    // logsDOM.scrollTo({ top: logsDOM.scrollHeight });
    // if (logsDOM.children.length > 50) logsDOM.children[0].remove();

    if (event == "clickNode") {
        console.log("click to load node:", item)
        epik.explorer([item])
    }
}

export default function handleGraphEvents(graph: Graph, epik: Epik, hoveredEdge: any, renderer: Sigma) {
    ["enterNode", "leaveNode", "clickNode", "rightClickNode", "doubleClickNode", "wheelNode"].forEach((eventType) =>
        renderer.on(eventType, ({ node }) => logEvent(graph, epik, eventType, "node", node)),
    );
    ["clickEdge", "rightClickEdge", "doubleClickEdge", "wheelEdge"].forEach((eventType) =>
        renderer.on(eventType, ({ edge }) => logEvent(graph, epik, eventType, "edge", edge)),
    );
    renderer.on("enterEdge", ({ edge }) => {
        logEvent(graph, epik, "enterEdge", "edge", edge);
        hoveredEdge = edge;
        renderer.refresh();
    });
    renderer.on("leaveEdge", ({ edge }) => {
        logEvent(graph, epik, "leaveEdge", "edge", edge);
        hoveredEdge = null;
        renderer.refresh();
    });
}